// Component like
@include component(like) {

	// Option --smiley
	@include option(smiley) {
		position: relative;
		border-radius: 50%;
		border: 2px solid #999999;
		width: 22px;
		height: 22px;

		transition: border 0.1s;

		// Part __left, __right, __smile
		@include part(left, right, smile) {
			display: block;
			position: absolute;
			border-radius: 50%;
			border: 1px solid #999999;
			transform: rotate(45deg);

			transition: border 0.1s, width 0.1s, height 0.1s;
		}

		// Part __left, __right
		@include part(left, right) {
			top: 4px;
			width: 3px;
			height: 3px;
		}

		// Part __left
		@include part(left) {
			left: 4px;
		}

		// Part __right
		@include part(right) {
			right: 4px;
		}

		// Part __smile
		@include part(smile) {
			bottom: 3px;
			left: 4px;
			border-top: 1px solid transparent;
			border-left: 1px solid transparent;
			width: 10px;
			height: 10px;
		}

		// Option --active
		@include option(active) {
			border: 2px solid $brand-secondary-color;

			// Part __left, __right, __smile
			@include part(left, right, smile) {
				border: 1px solid $brand-secondary-color;
			}

			// Part __left, __right
			@include part(left, right) {
				top: 4px;
				border-bottom: 1px solid transparent;
				border-right: 1px solid transparent;
				width: 5px;
				height: 5px;
			}

			// Part __left
			@include part(left) {
				left: 3px;
			}

			// Part __right
			@include part(right) {
				right: 3px;
			}

			// Part __smile
			@include part(smile) {
				border-top: 2px solid transparent;
				border-left: 2px solid transparent;
			}
		}
	}
}