$leaderboard-colour-stop: 600 !default;

// Component main
@include component(main) {
	position: relative;
	padding-top: $header-height;

	// Part __main
	@include part(main) {

		@media all and (min-width: $screen-lg-min) {
			background-color: $container-primary-color; // not needed
			position: relative;
				//z-index: 9; // lower than aside
			padding: 0 10px 0 30px;
			padding-top: 25px;

			float: right;
			width: 70%;
		}
	}

	// Part __aside
	@include part(aside) {
		position: relative;

		@media all and (min-width: $screen-lg-min) {
			background-color: #ffffff;
			float: left;
			position: relative;
				z-index: 10;
			padding: 0 30px 0 10px;
			margin-top: 0 !important;
			padding-top: 25px;
			width: 30%;

			transition: margin-top 0s;
		}

		// Part __trigger
		@include part(trigger) {
			background-color: $container-primary-color;
			display: none;
			position: absolute;
				top: 100%;
				right: 0;
			border-radius: 0 0 4px 4px;
			box-shadow: 0 2px 1px 0 rgba(0, 0, 0, 0.35);
			padding: 10px 20px;
		}

		// Option --pulldown
		@include option(pulldown) {
			background-color: $container-primary-color;

			transition: margin-top 0.3s;

			@media all and (min-width: $screen-lg-min) {
				background-color: #ffffff;
			}

			// background: aside pulldown
			&:before {
				content: "";
				background-color: $container-primary-color;
				position: absolute;
					left: -50%;
					top: 0;
					z-index: 0;
				box-shadow: 0 2px 1px 0 rgba(0, 0, 0, 0.35);
				width: 200%;
				height: 100%;

				@media all and (min-width: $screen-lg-min) {
					display: none;
				}
			}

			// Part __trigger
			//@include part(trigger) {
			.main__aside__trigger {
				display: block;

				@media all and (min-width: $screen-lg-min) {
					display: none;
				}
			}
		}
	}

	// Option --two-columns
	@include option(two-columns) {

		@media all and (min-width: $screen-lg-min) {
			background-color: $container-primary-color;
		}

		// Part __main
		@include part(main) {

			// background: aside
			&:before {
				content: "";
				background-color: #ffffff;
				position: absolute;
				right: 100%;
				top: 0;
				z-index: 0;
				height: 200%;
				width: 100%;
			}
		}
	}

	// Option --leaderboard
	@include option(leaderboard) {

		background: -moz-linear-gradient(top, #69c7ed 0%, #ffffff ($leaderboard-colour-stop + 0px)); /* FF3.6+ */
		background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#69c7ed), color-stop($leaderboard-colour-stop,#ffffff)); /* Chrome,Safari4+ */
		background: -webkit-linear-gradient(top, #69c7ed 0%, #ffffff ($leaderboard-colour-stop + 0px)); /* Chrome10+,Safari5.1+ */
		background: -o-linear-gradient(top, #69c7ed 0%, #ffffff ($leaderboard-colour-stop + 0px)); /* Opera 11.10+ */
		background: -ms-linear-gradient(top, #69c7ed 0%, #ffffff ($leaderboard-colour-stop + 0px)); /* IE10+ */
		background: linear-gradient(to bottom, #69c7ed 0%, #ffffff ($leaderboard-colour-stop + 0px)); /* W3C */

		// ie style
		.ie8 & {
			background: #ffffff; /* Old browsers */
			background-image: url(../images/sky.gif);
			background-position: left top;
			background-repeat: repeat-x;
		}

		// Component heading-row
		@include component(heading-row) {
			> h1,
			> h2,
			> h3,
			> h4,
			> h5,
			> h6 {
				color: #ffffff;
				text-align: center;
			}

			h1 {
				font-size: 42px;
				margin-top: 10px;
				margin-bottom: 10px;
			}

			h2 {
				font-size: 22px;
				margin-bottom: 40px;
			}
		}
	}
}