// Component footer
@include component(footer) {
	background-color: $footer-color;
	text-align: center;
	z-index: $zindex-footer;

	// always make footer show on bottom of viewport
	display: table-row;
	width: 100%;
	height: $footer-height;

	@media all and (min-width: $screen-sm-min) {
		text-align: left;
	}

	.containerHolder {
		background-color: $footer-color;
		position: relative;
			z-index: $zindex-footer;
	}

	.container {
		@extend %clearfix;

		padding: 1.5em 0;
	}

	// Component logo
	@include component(logo) {

		@media all and (min-width: $screen-sm-min) {
			margin: 1em 0;
		}
	}

	// Part __copyright
	@include part(copyright) {
		color: $text-primary-color;
		display: block;
		font-size: 0.85em;
		text-align: center;
		margin: 1em 0;

		@media all and (min-width: $screen-sm-min) {
			text-align: right;
			margin: 0;
		}
	}

	// Part __menu
	@include part(menu) {
		font-size: 0.85em;
		text-align: center;
		margin: 2em 0 0 0;

		@media all and (min-width: $screen-sm-min) {
			text-align: right;
			margin: 1em 0 0 0;
		}

		// Part __item
		@include part(item) {
			color: #ffffff;
			display: inline-block;
			margin: 0 1em 1em 1em;
			
			a:hover {
				text-decoration: underline;
			}

			@media all and (min-width: $screen-sm-min) {
				&:last-child {
					margin-right: 0;
				}
			}
		}
	}
}