// Component field
@include component(field) {

	// Part __color
	@include part(color) {

		// Part __trigger
		@include part(trigger) {
			background-color: transparent;
			position: absolute;
			right: 0;
			bottom: 7px;
			border: 1px solid $input-border-color;
			width: 30px;
			height: 30px;
		}
	}
}

// Color picker additional css
.cp-color-picker {
	z-index: $zindex-color;
}
.cp-alpha {
	display: none !important;
}