// Component form
@include component(form) {

	// Component button-holder
	@include component(button-holder) {
		margin-top: 15px;
		margin-bottom: 15px;
	}
}

// Component field
@include component(field) {
	@extend %clearfix;

	position: relative;
	margin-bottom: 20px;

	// Style Placeholders
	::-webkit-input-placeholder {
		color: $text-primary-color-lighter;
	}
	:-moz-placeholder { /* Firefox 18- */
		color: $text-primary-color-lighter;
	}
	::-moz-placeholder { /* Firefox 19+ */
		color: $text-primary-color-lighter;
	}
	:-ms-input-placeholder {
		color: $text-primary-color-lighter;
	}

	// Part __label
	@include part(label) {
		color: #000000;
		line-height: 1.4;

		strong {
			color: $text-secondary-color;
			font-weight: normal;
		}
	}

	// Part __info
	@include part(info) {
		color: $text-primary-color;
		font-size: 0.8em;
		line-height: 1.8;
		margin: 1em 0 2.5em 0;
	}

	// Part __group
	@include part(group) {
		margin: 20px 0;
	}

	// Part __wrapper
	@include part(wrapper) {
		position: relative;
	}

	// Part __sub
	@include part(sub) {
		@extend %clearfix;

		margin: 0 0 $checkable-margin 0;

		// extra margin for sub following label
		label + & {
			margin-top: $checkable-margin * 1.5;
		}
	}

	// Part __text, __textarea, __select, __color, __autosuggest
	@include part(text, textarea, select, color, autosuggest) {
		background-color: transparent;
		line-height: 1.4;
		outline: none;
		border-radius: 0;
		border: 1px solid #ffffff; // remove browser default borders
		border-top: 0;
		border-right: 0;
		border-left: 0;
		border-bottom: 1px solid $input-border-color;
		padding: 0;
		width: 100%;
		height: $input-field-height;

		transition: border-bottom 0.3s, box-shadow 0.3s;

		&:focus {
			border-bottom: 1px solid $input-focus-color;
			box-shadow: 0 1px 0 0 $input-focus-color;
		}

		// Option --text-left
		@include option(text-left) {
			text-align: left;
		}

		// Option --text-center
		@include option(text-center) {
			text-align: center;
		}

		// Option --text-right
		@include option(text-right) {
			text-align: right;
		}
	}

	// Part __text, __textarea, __color, __autosuggest
	@include part(text, textarea, color, autosuggest) {
		padding-left: 5px;
		padding-right: 5px;
	}

	// Part __checkbox, __radio
	@include part(checkbox, radio) {
		position: absolute;
		left: -9999px;

		+ label {
			color: $checkable-label-color;
			cursor: pointer;
			//display: inline-block;
			float: left;
			font-size: 1rem;
			line-height: $checkable-size;
			overflow: hidden;
			position: relative;
			padding-left: $checkable-padding-left;
			height: auto;
			min-height: $checkable-size;

			// icon
			&:before,
			&:after {
				content: "";
				position: absolute;
			}
		}
	}

	// Part __value
	@include part(value) {
		color: $text-tertiary-color;
		display: block;
		line-height: 1.4;
		margin-top: 1em;
		margin-bottom: 1em;

		@media all and (min-width: $screen-sm-min) {
			float: right;
			text-align: right;
			margin-top: 5px;
			margin-bottom: 5px;
		}

		a {
			color: $text-secondary-color;

			&:hover {
				color: $text-tertiary-color;
			}
		}
	}

	// Option --left, --right
	@include option(left, right) {

		// Part __label
		@include part(label) {
			float: left;
			margin-right: 10px;
			padding-top: 5px;
			padding-bottom: 5px;

			// elements that come after the label
			+ * {
				clear: both;
				float: left;

				@media all and (min-width: $screen-sm-min) {
					clear: none;
				}
			}
		}
	}

	// Option --right
	@include option(right) {

		// Part __label
		@include part(label) {
			+ * {
				@media all and (min-width: $screen-sm-min) {
					float: right;
				}
			}
		}
	}

	// Option --small
	@include option(small) {

		// Part __text, __textarea, __select, __color
		@include part(text, textarea, select, color) {
			height: $input-field-height-small;
		}
	}
}