// Component typography
@include component(typography) {
	line-height: 1.5;

	h1, h2, h3, h4 {
		color: $heading-primary-color;
		font-weight: bold;
		line-height: 1.2;
		margin: 0;
		margin-bottom: 1.2em;
		padding: 0;
	}

	h1 {
		font-size: $heading1-size;
	}

	h2 {
		font-size: $heading2-size;
		padding-top: 0.5em;
	}

	h3 {
		font-size: $heading3-size;
		text-transform: uppercase;
		padding-top: 0.5em;
	}

	p + h3 {
		padding-top: 2em;
	}

	h4 {
		font-size: $heading4-size;
		padding-top: 1em;
	}

	p {
		line-height: 1.8;
		margin-bottom: 1.6em;
	}

	a {
		color: $brand-primary-color;
	}
	a:hover {
		text-decoration: underline;
	}

	strong {
		font-weight: bold;
	}

	em {
		font-style: italic;
	}

	ul, ol {
		margin-bottom: 2em;
		padding-left: 1.2em;
	}

	ul {
		list-style-position: outside;
	}

	li {
		margin: 0.75em 0 1.5em 0;
		padding-left: 1.3em;
	}

	ul li {
		list-style-type: disc;
	}
}