$comment-write-author-size: 30px;
$comment-write-author-size-md: 40px;

// Component comment-write
@include component(comment-write) {
	@extend %clearfix;

	position: relative;
	margin: 0 auto;
	padding: 10px 0;

	// Part __message
	@include part(message) {
		position: relative;
		min-height: $comment-write-author-size;

		@media all and (min-width: $screen-md-min) {
			min-height: $comment-write-author-size-md;
		}

		// Part __author
		@include part(author) {
			position: absolute;
				left: 10px;
				top: 0;
				z-index: 10;
			width: $comment-write-author-size;
			height: $comment-write-author-size;

			@media all and (min-width: $screen-md-min) {
				width: $comment-write-author-size-md;
				height: $comment-write-author-size-md;
			}
		}

		// Component field
		@include component(field) {
			margin-left: ($comment-write-author-size + 20);
			margin-right: 10px;
			margin-bottom: 0;
			padding-right: 80px; // dodge the "post" button

			@media all and (min-width: $screen-md-min) {
				margin-left: ($comment-write-author-size-md + 20);
			}

			// Part __textarea
			@include part(textarea) {
				font-size: 0.85em;
				border-bottom: 0;
				padding: 0;
				min-height: 40px;
				height: 40px;
			}
		}

		// Component button-holder
		@include component(button-holder) {
			position: absolute;
				right: 10px;
				top: 4px;
			margin: 0;
		}
	}
}