// Component field
@include component(field) {

	// Part __radio
	@include part(radio) {
		// outer, inner
		+ label:before,
		+ label:after {
			@extend %circle;

			left: 0;
			top: 50%;
			margin-top: -($checkable-size / 2);
			z-index: 0;
			margin-left: 2px;
			width: $checkable-size;
			height: $checkable-size;

			transition: 0.2s ease;
		}

		// outer
		+ label:before {
			border: 2px solid $radio-default-color;
		}
		&:checked {
			+ label:before {
				background-color: $radio-fill-color;
				border: 2px solid $radio-stroke-color;
			}
		}

		// inner
		&:checked {
			+ label:after {
				background-color: $radio-inner-color;
				border: 2px solid $radio-inner-color;
				transform: scale(.5);
			}
		}
	}
}