$email-notification-sender-size:    60px !default;
$email-notification-sender-size-sm: 120px !default;

// Component email-notification
@include component(email-notification) {
	@extend %clearfix;

	position: relative;
	margin-bottom: 10px;

	// Part __title
	@include part(title) {
		color: $heading-primary-color;
		font-size: 1.4em;
		margin-top: 0.8em;
		margin-bottom: 0.5em;

		@media all and (max-width: $screen-sm-min) {
			margin-top: 0.2em;
		}
	}

	// Part __sender
	@include part(sender) {
		vertical-align: top;
		padding: 0px 20px;

		img {
			display: block;
			border: 1px solid $border-secondary-color;
			max-width: none;
			max-height: none;
			width: $email-notification-sender-size-sm;
			height: $email-notification-sender-size-sm;
		}

		@media all and (max-width: $screen-sm-min) {
			img {
				width: $email-notification-sender-size;
				height: $email-notification-sender-size;
			}
		}
	}

	// Part __details
	@include part(details) {
		padding-right: 20px;
	}

	// Part __description
	@include part(description) {
		color: $text-secondary-color;
		line-height: 1.6;
		margin-top: 0.8em;
		margin-bottom: 1.8em;
	}
}