@include component(tag) {
	background: $brand-tertiary-color-lighter;
	color: $brand-secondary-color-light;
	display: inline-block;
	font-size: 14px;
	vertical-align: top;
	border-radius: 4px;
	padding: 10px 15px;

	transition: color 0.3s;

	&:hover {
		color: $brand-secondary-color;
	}

	// Part __text
	@include part(text) {
		line-height: 1.1;
		vertical-align: middle;
	}

	// Part __close
	@include part(close) {
		cursor: pointer;
		font-size: 120%;
		line-height: 1.2;
		vertical-align: middle;

		// Default --right
		margin-left: 10px;
		margin-right: 0;

		// Option --left
		@include option(left) {
			margin-left: 0;
			margin-right: 10px;
		}
	}
}