$post-write-author-size: 40px;

// Component post-options
@include component(post-options) {
	@extend %clearfix;

	width: 100%;

	.fa {
		color: $brand-secondary-color;
		font-size: 1.5em;
		vertical-align: middle;
	}

	// Part __group
	@include part(group) {

		// Option --left
		@include option(left) {
			float: left;
			text-align: left;
			width: 50%;
		}

		// Option --right
		@include option(right) {
			float: right;
			text-align: right;
			width: 50%;
		}
	}

	// Part __action
	@include part(action) {
		color: $brand-secondary-color;
		display: inline-block;
		position: relative;
		vertical-align: middle;
		margin: 0 15px;

		// Part __label
		@include part(label) {
			display: inline-block;
			font-size: 0.85em;
			vertical-align: middle;
			margin-top: 2px;
			margin-left: 6px;
		}
	}

	// Part __sharing
	@include part(sharing) {
		width: 100%;

		// Component dropdown
		@include component(dropdown) {
			top: -5px;

			// Part __content
			@include part(content) {

				// Component field
				@include component(field) {
					margin-bottom: 0;
					padding: 20px 20px 0 20px;

					// Part __group
					@include part(group) {
						column-count: 1;

						@media all and (min-width: $screen-sm-min) {
							column-count: 2;
						}
					}
				}

				// Component button-holder
				@include component(button-holder) {
					padding: 10px;
				}
			}
		}
	}

	// Part __comments
	@include part(comments) {

		// Component itemlist
		@include component(itemlist) {
			margin-bottom: 0;
		}
	}
}