// Component dialog
@include component(dialog) {
	opacity: 0;
	pointer-events: none;
	position: fixed;
		left: 0;
		top: 0;
		z-index: $zindex-modal;
	width: 100%;
	height: 0;

	transition: opacity 0.2s;

	// ie style
	.ie8 & {
		overflow: hidden;
	}

	// Part __content
	@include part(content) {
		display: none;
	}

	// Part __underlay
	@include part(underlay) {
		background-color: #000000;
		opacity: 0.4;
		position: fixed;
		left: 0;
		top: 0;
		z-index: 5;
		width: 100%;
		height: 100vh;
	}

	// Part __overlay
	@include part(overlay) {
		background-color: #ffffff;
		overflow: auto;
		position: relative;
			top: 200px;
			z-index: 10;
		border-radius: 5px;
		border: 1px solid $border-secondary-color;
		margin: 0 auto;
		width: 90%;
		max-height: 90vh;

		transition: top 0.2s;

		@media all and (min-width: $screen-sm-min) {
			width: 80%;
		}

		@media all and (min-width: $screen-md-min) {
			width: 70%;
		}

		@media all and (min-width: $screen-lg-min) {
			width: 60%;
		}

		@media all and (min-width: $screen-xl-min) {
			width: 50%;
		}

		// Part __content
		@include part(content) {
			padding: 30px 20px;

			@media all and (min-width: $screen-sm-min) {
				padding-top: 40px;
				padding-bottom: 40px;
			}
		}
	}

	// Option --active
	@include option(active) {
		opacity: 1;
		overflow: visible;
		pointer-events: auto;
	}

	// Option --animating
	@include part(animating) {
		overflow: visible;
		pointer-events: auto;
	}
}