$message-author-size:    40px;
$message-author-size-sm: 80px;

// Component message
@include component(message) {
	@extend %clearfix;

	position: relative;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 20px;
	//max-width: 600px;

	// Part __body
	@include part(body) {
		position: relative;
		margin-bottom: 10px;
		min-height: $message-author-size;

		@media all and (min-width: $screen-sm-min) {
			min-height: $message-author-size-sm;
		}

		// Part __avatar
		@include part(avatar) {
			position: absolute;
				left: 0;
				top: 0;
				z-index: 10;
			width: $message-author-size;
			height: $message-author-size;

			@media all and (min-width: $screen-sm-min) {
				width: $message-author-size-sm;
				height: $message-author-size-sm;
			}
		}

		// Part __sender, __content
		@include part(sender, content) {
			border: 0;
			border-left: $message-author-size solid transparent;
			padding-left: 10px;

			@media all and (min-width: $screen-sm-min) {
				border-left: $message-author-size-sm solid transparent;
			}
		}

		// Part __sender
		@include part(sender) {
			float: left;
			font-size: 0.8em;
			margin-bottom: 0.5em;

			@media all and (min-width: $screen-sm-min) {
				font-size: 1em;
			}
		}

		// Part __avatar
		@include part(avatar) {
			border: 1px solid $border-secondary-color;
		}

		// Part __content
		@include part(content) {
			clear: both;
			color: $text-secondary-color;
			line-height: 1.6;
			border: 1px solid $border-secondary-color;
			margin-left: ($message-author-size + 10);
			margin-top: 0.8em;
			margin-bottom: 0.8em;
			padding: 10px;

			@media all and (min-width: $screen-sm-min) {
				margin-left: ($message-author-size-sm + 10);
			}

			a {
				color: $brand-primary-color;
			}
		}

		// Part __timestamp
		@include part(timestamp) {
			color: $text-tertiary-color;
			float: right;
			font-size: 0.8em;
			margin-bottom: 0.5em;
		}

		// Part __assets
		@include part(assets) {
			clear: both;
			margin-left: ($message-author-size + 10);

			@media all and (min-width: $screen-sm-min) {
				margin-left: ($message-author-size-sm + 10);
			}
		}
	}
}