// Component heading-row
@include component(heading-row) {
	@extend %clearfix;

	clear: both;
	margin-top: 1em;
	margin-bottom: 1em;
	padding: 15px 10px;

	// heading itself
	> h1,
	> h2,
	> h3,
	> h4,
	> h5,
	> h6 {
		font-weight: 500;
	}

	// Part __heading
	@include part(heading) {
		float: left;
	}

	// Option --primary
	@include option(primary) {
		background-color: transparent;
		color: $heading-primary-color;

		// heading itself
		> h1,
		> h2,
		> h3,
		> h4,
		> h5,
		> h6 {
			font-weight: 700;
		}
	}

	// Option --secondary
	@include option(secondary) {
		background-color: lighten($brand-tertiary-color-lighter, 3%);
		color: $heading-primary-color;
		border-bottom: 2px solid lighten($brand-secondary-color-lighter, 20%);
	}

	// Option --center
	@include option(center) {
		text-align: center;

		// Part __heading
		@include part(heading) {
			float: none;
		}
	}
}

// Component main
@include component(main) {

	// Component heading-row
	@include component(heading-row) {

		// Option --secondary
		@include option(secondary) {

			@media all and (min-width: $screen-lg-min) {
				background-color: lighten($brand-secondary-color-lighter, 29%);
			}
		}
	}
}