$email-max-width: 600px !default;

// Component email-layout
@include component(email-layout) {
	background-color: $email-color;
	overflow-x: hidden;
	width: 100% !important;
	height: 100%;
}

// Component email-container
@include component(email-container) {
	display: block;
	margin: 0 auto;
	max-width: $email-max-width;

	// Component logo
	@include component(logo) {
		background-image: none !important;
		text-indent: 0 !important;
		height: auto !important;
	}

	// Part __content
	@include part(content) {
		display: block;
		margin: 0 auto;
		padding: 1.5em 0;
		max-width: $email-max-width;

		table {
			width: 100%;
		}
	}
}

// Component email-header
@include component(email-header) {
	text-align: center;
	width: 100%;

	// Component email-container
	@include component(email-container) {

		// Part __content
		@include part(content) {
			table {
				border: 1px solid $border-secondary-color;
				border-bottom: 3px solid darken($border-secondary-color, 5%);
			}
		}
	}

	// Component logo
	@include component(logo) {
		margin: 25px 0;
	}
}

// Component email-footer
@include component(email-footer) {
	text-align: center;
	width: 100%;

	// Component email-container
	@include component(email-container) {

		// Part __content
		@include part(content) {
			table {
				border: 1px solid $border-secondary-color;
				border-bottom: 3px solid darken($border-secondary-color, 5%);
			}
		}
	}

	// Component logo
	@include component(logo) {
		margin-top: 25px;

		img {
			width: 80%;
		}
	}

	// Part __menu
	@include part(menu) {
		font-size: 0.85em;
		text-align: center;
		margin: 2em 0 0 0;

		// Part __item
		@include part(item) {
			color: #ffffff;
			display: inline-block;
			margin: 0 1em 1em 1em;

			a:hover {
				text-decoration: underline;
			}
		}
	}

	// Part __copyright
	@include part(copyright) {
		color: $text-primary-color;
		display: block;
		font-size: 0.85em;
		text-align: center;
		margin: 1em 0 25px 0;
	}
}

// Component email-main
@include component(email-main) {
	width: 100%;

	// Component email-container
	@include component(email-container) {
		padding: 0;

		// Part __content
		@include part(content) {
			background-color: #ffffff;
			border: 1px solid $border-secondary-color;
			border-bottom: 3px solid darken($border-secondary-color, 5%);
			padding: 1.5em 0;
		}
	}

	// Component email-intro
	@include component(email-intro) {
		text-align: center;

		// Part __title
		@include part(title) {
			color: $text-secondary-color;
			margin: 0.8em 0;
			padding: 0 30px;
		}

		// Part __message
		@include part(message) {
			color: $text-secondary-color;
			font-size: 1.2em;
			line-height: 1.7;
			margin-bottom: 1.6em;
			padding: 0 30px;
		}
	}

	// Component button-holder
	@include component(button-holder) {
		margin-bottom: 20px;

		// Component button
		@include component(button) {
			margin: 0 10px;
		}
	}
}