// Tweaks

// Tweak +right
@include tweak(right) {
	float: right;
}

// Tweak +left
@include tweak(left) {
	float: left;
}

// Tweak +center
@include tweak(center) {
	text-align: center;
	margin-left: auto;
	margin-right: auto;
}

// Tweak +hidden
@include tweak(hidden) {
	display: none;
}

// Tweak +shown
@include tweak(shown) {
	display: block; // could use 'initial' here, but defaults to 'inline' (as per CSS spec) and has problems in IE10 or lower
}

@media screen and (min-width: $screen-sm-min) {
	// Tweak +hidden-sm
	@include tweak(hidden-sm) {
		display: none;
	}

	// Tweak +shown-sm
	@include tweak(shown-sm) {
		display: block; // could use 'initial' here, but defaults to 'inline' (as per CSS spec) and has problems in IE10 or lower
	}
}

