// Component attachment
@include component(attachment) {
	background-color: #ffffff;
	position: relative;
	box-shadow: 0 2px 1px 0 rgba(0, 0, 0, 0.1);

	// Part __content
	@include part(content) {
		> * {
			display: block;
			width: 100%;
		}
	}

	// Part __meta
	@include part(meta) {
		position: relative;
		border: 1px solid $border-secondary-color;
		padding: 20px 15px;

		@media all and (min-width: $screen-sm-min) {
			padding-left: 30px;
			padding-right: 30px;
		}

		// Part __title
		@include part(title) {
			color: $heading-primary-color;
			font-size: 1.5em;
			margin-bottom: 0.5em;
		}

		// Part __description
		@include part(description) {
			color: $text-primary-color;
			font-size: 0.85em;
			line-height: 1.5;
			margin-bottom: 0.6em;
		}

		// Part __source
		@include part(source) {
			color: $text-tertiary-color;
			font-size: 0.8em;
			position: absolute;
				right: 15px;
				bottom: 10px;
		}
	}

	// Part __link
	@include part(link) {
		position: absolute;
			left: 0;
			top: 0;
			z-index: 10;
		text-indent: -9999px;
		width: 100%;
		height: 100%;
	}

	// Option --interactive
	@include option(interactive) {

		// Part __content
		@include part(content) {
			position: relative;
			z-index: 20;
		}
	}
}