// Component header
@include component(header) {
	background-color: $header-color;
	position: fixed;
		left: 0;
		top: 0;
		z-index: $zindex-header;
	width: 100%;
	height: $header-height;

	// Component logo
	@include component(logo) {
		float: left;
		margin: 14px 0;
	}

	// Component menu
	@include component(menu) {
		text-align: left;
	}
}