$preview-max-height:   100px;
$preview-max-width:    120px;
$preview-max-width-sm: 200px;

// Component filelist
@include component(filelist) {

	// Part __input
	@include part(input) {

		// Option --new
		@include option(new) {

			// Part __notes
			//@include part(notes) {
			.filelist__input__notes {
				display: none;
			}
		}
	}

	// Option --inline-view
	@include option(inline-view) {
		@extend %clearfix;

		// Part __input, __preview
		@include part(input, preview) {
			float: left;
			padding: 10px;
		}

		// Part __preview
		@include part(preview) {

			// Part __thumb
			@include part(thumb) {
				overflow: hidden;
				height: $preview-max-height;

				img {
					display: block;
					border: 1px solid $border-tertiary-color;
					max-height: 100%;
				}
			}
		}
	}

	// Option --list-view
	@include option(list-view) {
		@extend %clearfix;

		// Part __preview
		@include part(preview) {
			@extend %clearfix;
			margin-bottom: 20px;

			// Part __thumb
			@include part(thumb) {
				float: left;
				width: $preview-max-width;

				@media all and (min-width: $screen-sm-min) {
					width: $preview-max-width-sm;
				}

				img {
					display: block;
					border: 1px solid $border-tertiary-color;
				}
			}

			// Part __notes
			@include part(notes) {
				display: block;
				line-height: 1.5;
				margin-left: ($preview-max-width + 10);
				padding-top: 10px;

				@media all and (min-width: $screen-sm-min) {
					margin-left: ($preview-max-width-sm + 10);
				}
			}
		}
	}
}