$comment-author-size:    30px;
$comment-author-size-sm: 40px;

// Component comment
@include component(comment) {
	@extend %clearfix;

	position: relative;

	// Part __message
	@include part(message) {
		position: relative;
		border-bottom: 1px solid $border-secondary-color;
		padding: 10px;
		min-height: $comment-author-size;

		@media all and (min-width: $screen-sm-min) {
			min-height: $comment-author-size-sm;
		}

		// Part __author
		@include part(author) {
			position: absolute;
				left: 10px;
				top: 10px;
				z-index: 10;
			width: $comment-author-size;
			height: $comment-author-size;

			@media all and (min-width: $screen-sm-min) {
				width: $comment-author-size-sm;
				height: $comment-author-size-sm;
			}
		}

		// Part __event, __content
		@include part(event, content) {
			border: 0;
			border-left: $comment-author-size solid transparent;
			padding-left: 10px;

			@media all and (min-width: $screen-sm-min) {
				border-left: $comment-author-size-sm solid transparent;
			}
		}

		// Part __event
		@include part(event) {
			color: $text-secondary-color;
			font-size: 0.8em;
			line-height: 1.2;
			margin-bottom: 0.2em;

			a {
				color: $brand-primary-color;
				&:hover {
					text-decoration: underline;
				}
			}
		}

		// Part __timestamp
		@include part(timestamp) {
			color: $text-tertiary-color;
			font-size: 0.8em;
			position: absolute;
				right: 10px;
				top: 10px;
			margin-bottom: 0.2em;
		}

		// Part __content
		@include part(content) {
			color: $text-secondary-color;
			font-size: 0.8em;
			line-height: 1.6;
			margin-top: 0.8em;

			a {
				color: $brand-tertiary-color;
			}
		}
	}
}