// Component itemlist
@include component(itemlist) {
	@extend %clearfix;

	margin-bottom: 20px;

	ul {
		@extend %clearfix;
	}

	// Part __filters
	@include part(filters) {
		@extend %clearfix;

		margin-bottom: 50px;
		padding-top: 20px;
	}

	// Part __group
	@include part(group) {
		margin-bottom: 60px;

		// Part __name
		@include part(name) {
			color: $text-tertiary-color;
			font-size: 1em;
			text-transform: uppercase;
			border-bottom: 1px solid $border-secondary-color;
			margin-bottom: 20px;
			padding-bottom: 20px;
		}
	}

	// Part __more
	@include part(more) {

		&:hover {
			text-decoration: underline;
		}

		// Component button-holder
		@include component(button-holder) {
			margin-bottom: 50px;
		}

		// Option --right
		@include option(right) {
			float: right;
			font-size: 0.8em;
			margin: 1em 0;
			padding: 4px 0;
		}
	}
}