// derived values
$switch-knob-margin: ($switch-height - $switch-knob-size) / 2;
$switch-knob-delta:  ($switch-width - $switch-knob-margin * 2 - $switch-knob-size);

// Component field
@include component(field) {

	// Part __switch
	@include part(switch) {
		margin-top: 1em;
		margin-bottom: 1em;
		width: $switch-width;
		height: $switch-height;

		@media all and (min-width: $screen-sm-min) {
			float: right;
			text-align: right;
			margin-top: 0;
			margin-bottom: 0;
		}

		// Part __label
		@include part(label) {
			background-color: $switch-default-color;
			cursor: pointer;
			float: left;
			position: relative;
			border-radius: 3px;
			width: 100%;
			height: 100%;

			transition: background-color 0.2s;

			// knob
			&:before {
				@extend %circle;

				content: "";
				background-color: $switch-inner-color;
				position: absolute;
					left: 0;
					top: 0;
				box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.22);
				margin: $switch-knob-margin;
				width: $switch-knob-size;
				height: $switch-knob-size;

				transition: left 0.2s;
			}
		}

		// Part __input
		@include part(input) {
			position: absolute;
				left: -9999px;

			// checked state
			&:checked {
				// switch
				+ label {
					background-color: $switch-fill-color;
				}

				// knob
				+ label:before {
					// shift knob to right side
					left: $switch-knob-delta;
				}
			}
		}
	}
}