// Component field
@include component(field) {

	// Part __file
	@include part(file) {
		display: none;

		// Part __remove-button
		@include part(remove-button) {
			background-color: transparent;
			color: $fileinput-remove-color;
			font-size: 120%;
			display: none;
			opacity: 0.6;
			position: absolute;
			right: 0;
			top: 50%;
			border-radius: 3px;
			border: 0;
			margin-top: -10px;
			width: 20px;
			height: 20px;

			transition: opacity 0.2s;

			&:hover {
				opacity: 1;
			}
		}

		// Option --has-file
		@include option(has-file) {
			position: relative;

			// remove button
			& + button {
				display: block;
			}
		}
	}
}