$slider-bar-height: 4px;
$slider-handle-size: 16px;

body.dragging {
	@extend %no-select;
}

// Component slider
@include component(slider) {
	position: relative;
	margin: 0 auto;
	margin-top: 20px;
		margin-bottom: 20px;
	padding-top: 2.5em;
	width: 90%;

	// Part __track
	@include part(track) {
		@extend %no-select;

		position: relative;
		border-bottom: $slider-bar-height solid $border-secondary-color;
		//margin-bottom: 20px;
		width: 100%;

		// Part __progress
		@include part(progress) {
			pointer-events: none;
			position: absolute;
				left: 0;
				top: 0;
				z-index: 1;
			border-bottom: $slider-bar-height solid $brand-secondary-color;
			width: 0;

			// Option --min
			@include option(min) {
				z-index: 2;
				border-bottom: $slider-bar-height solid $border-secondary-color;
			}

			// Option --range
			@include option(range) {
				border-bottom: $slider-bar-height solid $brand-secondary-color;
			}
		}
	}

	// Part __handle
	@include part(handle) {
		background-color: #ffffff;
		opacity: 0.75;
		cursor: pointer;
		position: absolute;
			left: 0;
			top: 50%;
			z-index: 5;
		border-radius: 3px;
		box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.5);
		margin-top: -6px;
		margin-left: -6px;
		width: $slider-handle-size;
		height: $slider-handle-size;

		transition: opacity 0.2s;

		// hover/active state
		&:hover,
		&:focus,
		&:active {
			opacity: 1;
			z-index: 10;
		}

		// active state
		&:focus,
		&:active {
			box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.9);
		}

		// Option --last-active
		@include option(last-active) {
			opacity: 1;
			z-index: 10;
			box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.9);
		}

		// Option --readonly
		@include option(readonly) {
			@extend %circle;

			background-color: $brand-secondary-color;
			cursor: default;
			opacity: 1;
			box-shadow: none;
			width: $slider-handle-size;
			height: $slider-handle-size;
		}
	}

	// Part __value, __label
	@include part(value, label) {
		font-size: 0.8em;

		// Option --min
		@include option(min) {
			float: left;
			margin-left: -5%;
		}

		// Option --max
		@include option(max) {
			float: right;
			margin-right: -5%;
		}

		// Option --single
		@include option(single) {
			position: absolute;
				bottom: 20px;
				left: 0;
			text-align: center;
			min-width: 40px;
			max-width: 70px;
		}

		// Option --align-left
		@include option(align-left) {
			text-align: left;
		}

		// Option --align-right
		@include option(align-right) {
			text-align: right;
		}
	}

	// Part __label
	@include part(label) {
		padding-top: 20px;
	}

	// Option --vertical
	@include option(vertical) {

	}
}

// Component field
@include component(field) {

	// Option --right
	@include option(right) {

		// Component slider
		@include component(slider) {
			float: right;
			margin-top: -20px;
			margin-bottom: 0;
			width: 45%;

			@media all and (min-width: $screen-sm-min) {
				clear: both;
				float: none;
				margin-top: 20px;
				margin-bottom: 20px;
				width: 90%;
			}
		}
	}
}