// Component field
@include component(field) {

	// Part __select
	@include part(select) {
		-webkit-appearance: none;
		-moz-appearance: none;
		appearance: none;
		padding-right: 22px;

		// Part __arrow
		@include part(arrow) {
			position: relative;

			// down arrow
			&:before {
				content: "\f107";
				color: $select-icon-color;
				font-family: $font-icons;
				font-size: 20px;
				pointer-events: none;
				position: absolute;
				right: 0;
				top: 50%;
				margin-top: -10px;
			}
		}
	}
}