$post-write-author-size: 40px;
$post-write-author-size-md: 80px;
$post-options-top-offset: -20px;

// Component post-write
@include component(post-write) {
	@extend %clearfix;

	position: relative;
	margin: 0 auto;
	padding: 20px 0 40px 0;
	max-width: 600px;

	// Part __message
	@include part(message) {
		position: relative;
		margin-bottom: 10px;
		padding-top: 1px;
		min-height: $post-write-author-size;

		@media all and (min-width: $screen-md-min) {
			min-height: $post-write-author-size-md;
		}

		// Part __author
		@include part(author) {
			position: absolute;
				left: 0;
				top: 0;
				z-index: 10;
			width: $post-write-author-size;
			height: $post-write-author-size;

			@media all and (min-width: $screen-md-min) {
				width: $post-write-author-size-md;
				height: $post-write-author-size-md;
			}
		}

		// Part __textbox
		@include part(textbox) {
			background-color: lighten($container-primary-color, 2%);
			box-shadow: inset 0 2px 5px 0 rgba(0, 0, 0, 0.15);
			border-radius: 4px;
			margin-top: ($post-write-author-size + 10);
			margin-bottom: 0;
			padding: 20px;
			min-height: $post-write-author-size;

			@media all and (min-width: $screen-md-min) {
				margin-top: ($post-write-author-size-md + 10);
			}

			// Part __textarea
			@include part(textarea) {
				background-color: transparent;
				display: block;
				font-size: 0.85em;
				line-height: 1.8;
				outline: none;
				overflow-y: hidden;
				resize: none;
				border: 0;
				min-height: $post-write-author-size;
				height: $post-write-author-size;
				width: 100%;
			}
		}
	}

	// Component post-options
	@include component(post-options) {
		position: absolute;
			right: 0;
			top: $post-options-top-offset;
		margin-top: $post-write-author-size;

		@media all and (min-width: $screen-md-min) {
			margin-top: $post-write-author-size-md;
		}

		// Part __sharing
		/*@include part(sharing) {
			position: absolute;
				right: 0;
				top: ($post-write-author-size + 20);

			@media all and (min-width: $screen-md-min) {
				top: ($post-write-author-size-md + 20);
			}
		}*/
	}

	// Option --message
	@include option(message) {
		//max-width: none;

		// Part __message
		@include part(message) {

			// Part __textbox
			@include part(textbox) {
				margin-left: ($post-write-author-size + 10);
				margin-top: 0;

				@media all and (min-width: $screen-md-min) {
					margin-left: ($post-write-author-size-md + 10);
				}
			}
		}

		// Component post-options
		@include component(post-options) {
			position: relative;
				top: auto;
			margin-top: 10px;
			margin-bottom: 10px;
		}
	}
}