//
// colour
// --------------------------

// Colour > Brand > Primary
$brand-primary-color:         #dc4020 !default;
$brand-primary-color-lighter: lighten($brand-primary-color, 30%) !default;
$brand-primary-color-light:   lighten($brand-primary-color, 18%) !default;
$brand-primary-color-dark:    darken($brand-primary-color, 18%) !default;
$brand-primary-color-darker:  darken($brand-primary-color, 30%) !default;

// Colour > Brand > Secondary
$brand-secondary-color:         #2a2c90 !default;
$brand-secondary-color-lighter: lighten($brand-secondary-color, 30%) !default;
$brand-secondary-color-light:   lighten($brand-secondary-color, 18%) !default;
$brand-secondary-color-dark:    darken($brand-secondary-color, 18%) !default;
$brand-secondary-color-darker:  darken($brand-secondary-color, 30%) !default;

// Colour > Brand > Tertiary
$brand-tertiary-color:         #6cadde !default;
$brand-tertiary-color-lighter: lighten($brand-tertiary-color, 30%) !default;
$brand-tertiary-color-light:   lighten($brand-tertiary-color, 18%) !default;
$brand-tertiary-color-dark:    darken($brand-tertiary-color, 18%) !default;
$brand-tertiary-color-darker:  darken($brand-tertiary-color, 30%) !default;

// Colour > Text
$text-primary-color:         #5e5e5e !default;
$text-primary-color-lighter: lighten($text-primary-color, 30%) !default;
$text-primary-color-light:   lighten($text-primary-color, 18%) !default;
$text-primary-color-dark:    darken($text-primary-color, 18%) !default;
$text-primary-color-darker:  darken($text-primary-color, 30%) !default;

$text-secondary-color:         #3a3a3a !default;
$text-secondary-color-lighter: lighten($text-secondary-color, 30%) !default;
$text-secondary-color-light:   lighten($text-secondary-color, 18%) !default;
$text-secondary-color-dark:    darken($text-secondary-color, 18%) !default;
$text-secondary-color-darker:  darken($text-secondary-color, 30%) !default;

$text-tertiary-color:         #969696 !default;
$text-tertiary-color-lighter: lighten($text-tertiary-color, 30%) !default;
$text-tertiary-color-light:   lighten($text-tertiary-color, 18%) !default;
$text-tertiary-color-dark:    darken($text-tertiary-color, 18%) !default;
$text-tertiary-color-darker:  darken($text-tertiary-color, 30%) !default;

// Colour > Heading
$heading-primary-color:   #000000 !default;
$heading-secondary-color: #90989b !default;
$heading-tertiary-color:  #bec0c1 !default;

// Colour > Border
$border-primary-color:   $brand-secondary-color-lighter !default;
$border-secondary-color: #e9eced !default;
$border-tertiary-color:  #e9eced !default;

// Colour > Layout
$header-color:  #1c1d1f !default;
$footer-color:  #1c1d1f !default;
$email-color:   #f8f8f8 !default;
$container-primary-color:   #f6f8f7 !default;
$container-secondary-color: #ffffff !default;

//
// form
// --------------------------

// Form > Text
$input-border-color:       $brand-secondary-color-lighter !default;
$input-focus-color:        $brand-secondary-color !default;
$input-field-height:       42px !default;
$input-field-height-small: 30px !default;

// Form > Checkable
$checkable-size:         18px !default;
$checkable-padding-left: 35px !default;
$checkable-label-color:  $text-tertiary-color !default;
$checkable-margin:       12px !default;

// Form > Radio
$radio-default-color: $border-primary-color !default;
$radio-stroke-color:  $brand-tertiary-color !default;
$radio-fill-color:    $brand-tertiary-color-lighter !default;
$radio-inner-color:   $brand-tertiary-color-darker !default;

// Form > Checkbox
$checkbox-default-color: $brand-tertiary-color-darker !default;
$checkbox-fill-color:    $brand-tertiary-color-lighter !default;
$checkbox-inner-color:   $brand-tertiary-color-darker !default;

// Form > Switch
$switch-default-color: $border-secondary-color !default;
$switch-fill-color:    $brand-secondary-color !default;
$switch-inner-color:   #ffffff !default;
$switch-width:         54px !default;
$switch-height:        30px !default;
$switch-knob-size:     16px !default;

// Form > Select
$select-icon-color: $text-primary-color !default;

// Form > File
$fileinput-remove-color: $brand-secondary-color-light !default;

//
// text
// --------------------------

// Text > Font
$font-primary: 'Museo-Sans', Arial, sans-serif !default;
$font-icons:   'FontAwesome' !default;
$font-size:    14px !default;

// Text > Heading
$heading1-size: 230% !default;
$heading2-size: 185% !default;
$heading3-size: 135% !default;
$heading4-size: 115% !default;

//
// layout
// --------------------------

// Layout > Header
$header-height:            60px !default;
$header-dropdown-width:    280px !default;
$header-dropdown-width-md: 400px !default;

// Layout > Footer
$footer-height: 100px !default;

// Layout > Menu
$menu-icon-color: $brand-secondary-color !default;

// Layout > Min Width
$screen-sm-min: 35.5em !default;
$screen-md-min: 48em !default;
$screen-lg-min: 64em !default;
$screen-xl-min: 80em !default;

// Layout > Max Width
$screen-xs-max: ($screen-sm-min - 0.1) !default;
$screen-sm-max: ($screen-md-min - 0.1) !default;
$screen-md-max: ($screen-lg-min - 0.1) !default;
$screen-lg-max: ($screen-xl-min - 0.1) !default;

// Layout > Container
$container-sm: ($screen-sm-max - 4) !default;
$container-md: ($screen-md-max - 4) !default;
$container-lg: ($screen-lg-max - 4) !default;

// Layout > Min Height
$screen-sm-min-height: 28.5em !default;
$screen-md-min-height: 42.5em !default;
$screen-lg-min-height: 54.5em !default;

// Layout > Max Height
$screen-xs-max-height: ($screen-sm-min-height - 0.1) !default;
$screen-sm-max-height: ($screen-md-min-height - 0.1) !default;
$screen-md-max-height: ($screen-lg-min-height - 0.1) !default;

// Layout > Z-index
$zindex-preview:              100 !default;
$zindex-color:                200 !default;
$zindex-pulldown:             300 !default;
$zindex-dropdown:             400 !default;
$zindex-autosuggest:          500 !default;
$zindex-main:                 940 !default;
$zindex-footer:               960 !default;
$zindex-header:               980 !default;
$zindex-header-pulldown:      1000 !default;
$zindex-header-dropdown:      1020 !default;
$zindex-header-menu-underlay: 1040 !default;
$zindex-header-menu:          1060 !default;
$zindex-modal-underlay:       1080 !default;
$zindex-modal:                1100 !default;

//
// grid
// --------------------------

// Grid > Gutter
$gutter-width: 10px !default;

//
// tabbing
// --------------------------

// Tabbing > Tabs
$tabbing-border-height: 2px !default;