// Component tabbing
@include component(tabbing) {

	// Part __nav
	@include part(nav) {
		@extend %clearfix;

		position: relative;
		border-bottom: $tabbing-border-height solid $border-secondary-color;
		margin: 0;
		width: 100%;
		height: auto;

		// All purecss grid column classes
		[class*="pure-u"] {
			padding: 0;
		}

		// Part __indicator
		@include part(indicator) {
			background-color: $brand-secondary-color;
			display: none;
			position: absolute;
			left: 0;
			bottom: -$tabbing-border-height;
			width: 0;
			height: $tabbing-border-height;

			transition: width 0.5s, left 0.5s;

			// Option --active
			@include option(active) {
				display: block;
			}
		}

		// Part __item
		@include part(item) {
			font-size: 0.8em;
			float: left;
			padding-left: 0;
			padding-right: 0;

			@media all and (min-width: $screen-sm-min) {
				font-size: 1em;
			}
		}

		// Part __tab
		@include part(tab) {
			display: block;
			text-align: center;
			text-transform: none;
			padding: 18px 15px;

			transition: border-bottom 0.3s,
			            background 0.3s;

			@media all and (min-width: $screen-sm-min) {
				padding-left: 30px;
				padding-right: 30px;
			}

			// Default --enabled
			color: $text-primary-color;
			cursor: pointer;

			// hover/active
			&:hover {
				color: $text-primary-color-dark;
			}

			// Option --active
			@include option(active) {
				color: $brand-tertiary-color-dark;
			}

			// Option --disabled
			@include option(disabled) {
				color: $text-primary-color-lighter;
				cursor: default;

				&:hover {
					background: transparent;
				}
			}
		}
	}

	// Part __panel
	@include part(panel) {
		margin-top: 1em;

		// Default --inactive
		display: none;

		// Option --active
		@include option(active) {
			display: block;
		}
	}
}

// Component main
@include component(main) {

	// Part __aside
	@include part(aside) {

		// Component tabbing
		@include component(tabbing) {

			// Part __nav
			@include part(nav) {

				@media all and (min-width: $screen-lg-min) {
					border-bottom: 0;

					// Part __indicator
					@include part(indicator) {

						// Option --active
						@include option(active) {
							display: none;
						}
					}

					// Part __item
					@include part(item) {
						border-bottom: $tabbing-border-height solid $border-secondary-color;
					}

					// Part __tab
					@include part(tab) {
						text-align: left;
					}
				}
			}
		}
	}
}