html {
	height: 100%;
}

// Component layout
@include component(layout) {
	overflow-x: hidden;
	height: 100%;

	// Part __wrapper
	@include part(wrapper) {
		display: table;
		overflow: hidden;
		width: 100%;
		height: 100%;

		@media all and (min-width: $screen-lg-min) {
			background-color: $container-primary-color;
		}
	}
}