// Component button
@include component(button-holder) {

	// Part __item
	@include part(item) {
		margin-bottom: 10px;
	}

	// Default --center
	text-align: center;

	// Option --left
	@include option(left) {
		text-align: left;
	}

	// Option --right
	@include option(right) {
		text-align: right;
	}
}

// Component button
@include component(button) {
	color: #ffffff;
	cursor: pointer;
	display: inline-block;
	font-weight: 300;
	line-height: 1.5;
	text-align: center;
	border-radius: 4px;
	box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.22);

	transition: background 0.2s, color 0.2s, border 0.2s;

	[class*="pure-u"] > & {
		width: 100%;
	}

	// Default --primary
	background: $brand-primary-color;
	border: 1px solid $brand-primary-color;
	&:hover {
		background: $brand-primary-color-light;
		border: 1px solid $brand-primary-color-light;
	}

	// Option --secondary
	@include option(secondary) {
		background: $brand-secondary-color;
		border: 1px solid $brand-secondary-color;
		&:hover {
			background: $brand-secondary-color-light;
			border: 1px solid $brand-secondary-color-light;
		}
	}

	// Option --tertiary
	@include option(tertiary) {
		//background: #ffffff;
		background: transparent;
		color: $brand-secondary-color-lighter;
		box-shadow: none;
		border: 1px solid $brand-secondary-color-lighter;
		&:hover {
			color: $brand-secondary-color;
			border: 1px solid $brand-secondary-color;
		}
	}

	// Default --medium
	font-size: 1.15em;
	padding: 10px 25px;

	// Option --small
	@include option(small) {
		font-size: 1em;
		padding: 7px 20px;
	}

	// Option --large
	@include option(large) {
		font-size: 1.3em;
		padding: 15px 35px;
	}

	// Part __text
	@include part(text) {
		line-height: 1.1;
		vertical-align: middle;
	}

	// Part __icon
	@include part(icon) {
		font-size: 130%;
		line-height: 1.2;
		vertical-align: middle;

		// Default --right
		float: right;
		padding-left: 10px;
		padding-right: 0;

		// Option --left
		@include option(left) {
			float: left;
			padding-left: 0;
			padding-right: 10px;
		}
	}
}