// Component gig-categories
@include component(gig-categories) {
	column-count: 2;
	column-fill: auto;

	@media all and (min-width: $screen-lg-min) {
		column-count: 3;
	}
}

// Component main
@include component(main) {

	// Part __aside
	@include part(aside) {

		// Component gig-categories
		@include component(gig-categories) {
			column-count: 2;
			column-fill: auto;

			@media all and (min-width: $screen-lg-min) {
				column-count: 1;
			}
		}
	}
}