// Component dropdown
@include component(dropdown) {
	position: relative;

	// Part __trigger
	@include part(trigger) {

		// beak
		&:after {
			content: "";
			background-color: #ffffff;
			display: none;
			pointer-events: none;
			position: absolute;
				bottom: -25px;
				right: 50%;
			margin-right: -10px;
			transform: rotate(45deg);
			width: 20px;
			height: 20px;
		}

		// Option --active
		@include option(active) {

			// beak
			&:after {
				display: block;
			}
		}
	}

	// Part __content
	@include part(content) {
		background-color: #ffffff;
		display: none;
		position: absolute;
			right: 0;
			top: 100%;
			z-index: $zindex-dropdown;

		// Option --transparent
		@include option(transparent) {
			background-color: transparent;
		}
	}

	// Option --active
	@include option(active) {

		// Part __trigger
		@include part(trigger) {

			// beak
			&:after {
				display: block;
			}
		}

		// Part __content
		@include part(content) {
			display: block;
		}
	}

	// Option --pulldown
	@include option(pulldown) {

		// Part __content
		@include part(content) {
			display: block;
			overflow: hidden;
			position: relative;
				right: 0;
				top: 0;
				z-index: $zindex-pulldown;
			height: 0;

			transition: height 0.35s;
		}

		// Option --open
		@include option(open) {

			// Part __content
			@include part(content) {
				transition: none;
				height: auto !important;
			}
		}
	}

	// Option --drop-shadow
	@include option(drop-shadow) {

		// Part __content
		@include part(content) {
			box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.35);
		}
	}
}