$notification-sender-size:    24px !default;
$notification-sender-size-sm: 40px !default;
$notification-padding-size:   10px !default;

// Component notification
@include component(notification) {
	@extend %clearfix;

	font-size: 0.8em;
	position: relative;
	margin-left: auto;
	margin-right: auto;
	padding: $notification-padding-size;
	max-width: 600px;

	&:hover {
		background-color: $brand-tertiary-color-lighter;
	}

	// Part __sender
	@include part(sender) {
		float: left;
		width: $notification-sender-size;
		height: $notification-sender-size;

		@media all and (min-width: $screen-sm-min) {
			width: $notification-sender-size-sm;
			height: $notification-sender-size-sm;
		}
	}

	// Part __message
	@include part(message) {
		float: right;
		margin-top: -$notification-sender-size;
		border-left: $notification-sender-size solid transparent;
		padding-top: 6px;
		padding-left: $notification-padding-size;
		width: 100%;
		min-height: $notification-sender-size;

		@media all and (min-width: $screen-sm-min) {
			margin-top: -$notification-sender-size-sm;
			border-left: $notification-sender-size-sm solid transparent;
			padding-top: 7px;
			min-height: $notification-sender-size-sm;
		}
	}

	// Part __timestamp
	@include part(timestamp) {
		color: $text-tertiary-color;
		float: right;
	}

	// Part __more
	@include part(more) {
		clear: both;
		display: block;
		border-top: 15px solid transparent;

		// Part --full
		@include option(full) {
			position: absolute;
				left: 0;
				top: 0;
			text-indent: -9999px;
			border-top: 0;
			width: 100%;
			height: 100%;
		}
	}
}