$checkbox-tick-unchecked-offset: 10px !default;
$checkbox-tick-checked-offset:   2px !default;

// Component field
@include component(field) {

	// Part __checkbox
	@include part(checkbox) {
		// tick, background
		+ label:before,
		+ label:after {
			left: 0;
			z-index: 1;
			margin-left: 2px;

			transition: border 0.2s, margin 0.2s, background-color 0.2s, top 0.2s 0.1s, left 0.2s 0.1s, width 0.2s 0.1s, height 0.2s 0.1s;
		}

		// tick
		+ label:before {
			left: 6px;
			top: 50%;
			margin-top: -($checkable-size / 2 - $checkbox-tick-unchecked-offset);
			border: 3px solid transparent;
			transform: rotateZ(37deg);
			transform-origin: 100% 100%;
			width: 0;
			height: 0;
		}
		&:checked {
			+ label:before {
				left: 2px;
				margin-top: -($checkable-size / 2 - $checkbox-tick-checked-offset);
				border-top: 2px solid transparent;
				border-left: 2px solid transparent;
				border-right: 2px solid $checkbox-inner-color;
				border-bottom: 2px solid $checkbox-inner-color;
				transform: rotateZ(37deg);
				transform-origin: 100% 100%;
				width:  $checkable-size * (1 / 3);
				height: $checkable-size * (2 / 3);
			}
		}

		// background
		+ label:after {
			background-color: transparent;
			top: 50%;
			margin-top: -($checkable-size / 2);
			z-index: 0;
			border-radius: 3px;
			border: 1px solid $checkbox-default-color;
			width: $checkable-size;
			height: $checkable-size;
		}
		&:checked {
			+ label:after {
				border: 1px solid $checkbox-fill-color;
				background-color: $checkbox-fill-color;
			}
		}
	}
}