$message-author-size:    40px !default;
$message-author-size-sm: 80px !default;
$event-avatar-size:      32px !default;

// Component event
@include component(event) {
	@extend %clearfix;

	position: relative;
	margin: 40px auto 50px auto;
	max-width: 600px;

	// Part __body
	@include part(body) {
		position: relative;
		margin-bottom: 10px;
		min-height: $event-avatar-size;
		margin-left: ($message-author-size + 10);

		@media all and (min-width: $screen-sm-min) {
			margin-left: ($message-author-size-sm + 10);
		}

		// Part __avatar
		@include part(avatar) {
			position: absolute;
				left: 0;
				top: 0;
			width: $event-avatar-size;
			height: $event-avatar-size;
		}

		// Part __avatar
		@include part(avatar) {
			border: 1px solid $border-secondary-color;
		}

		// Part __content, __timestamp
		@include part(content, timestamp) {
			font-size: 0.8em;
			line-height: 1.6;
			margin-top: 0.8em;
			margin-bottom: 0.8em;
		}

		// Part __content
		@include part(content) {
			color: $text-secondary-color;
			float: left;
			margin-left: ($event-avatar-size + 10);

			a {
				color: $brand-primary-color;
			}
		}

		// Part __timestamp
		@include part(timestamp) {
			color: $text-tertiary-color;
			float: right;
		}
	}
}