$email-gig-author-size:    60px !default;
$email-gig-author-size-sm: 80px !default;

// Component email-gig
@include component(email-gig) {
	@extend %clearfix;

	position: relative;
	margin-top: 2em;
	margin-bottom: 4em;

	// Part __title
	@include part(title) {
		color: $heading-primary-color;
		float: left;
		font-size: 1.4em;
		margin-bottom: 0.5em;
		width: 75%;

		@media all and (max-width: $screen-sm-min) {
			width: 65%;
		}
	}

	// Part __owner
	@include part(owner) {
		vertical-align: top;
		padding: 0px 10px 0px 20px;

		img {
			display: block;
			border: 1px solid $border-secondary-color;
			max-width: none;
			max-height: none;
			width: $email-gig-author-size-sm;
			height: $email-gig-author-size-sm;
		}

		@media all and (max-width: $screen-sm-min) {
			img {
				width: $email-gig-author-size;
				height: $email-gig-author-size;
			}
		}
	}

	// Part __details
	@include part(details) {
		padding-right: 20px;
	}

	// Part __deadline
	@include part(deadline) {
		color: $text-tertiary-color;
		display: block;
		float: left;
		margin-bottom: 0.5em;
		width: 70%;
	}

	// Part __description
	@include part(description) {
		clear: both;
		color: $text-secondary-color;
		line-height: 1.6;
		margin-top: 0.8em;
		margin-bottom: 0.8em;
	}

	// Part __reward
	@include part(reward) {
		color: $brand-primary-color;

		// Part __other
		@include part(other) {
			color: $brand-primary-color-dark;
			font-size: 80%;
		}
	}

	// Part __view
	@include part(view) {
		float: right;
	}
}