// outer container
.container {
	@extend %clearfix;

	//margin-left: $gutter-width;
	//margin-right: $gutter-width;
	margin-left: auto;
	margin-right: auto;
	padding-left: $gutter-width;
	padding-right: $gutter-width;

	@media (min-width: $screen-sm-min) {
		//margin-left: auto;
		//margin-right: auto;
		max-width: $container-sm;
	}
	@media (min-width: $screen-md-min) {
		max-width: $container-md;
	}
	@media (min-width: $screen-lg-min) {
		max-width: $container-lg;
	}

	// fluid
	&.container-fluid {
		max-width: 100%;
	}
}

// grid
.pure-g {
	@extend %clearfix;

	display: block;
	font-family: $font-primary;
	letter-spacing: normal;
	margin-left: -$gutter-width;
	margin-right: -$gutter-width;

	// no gutters
	&.pure-g-no-gutters {
		margin-left: 0;
		margin-right: 0;

		[class*="pure-u"] {
			padding-left: 0;
			padding-right: 0;
		}
	}

	// grid unit
	[class*="pure-u"] {
		float: left;
		font-family: $font-primary;
		padding-left: $gutter-width;
		padding-right: $gutter-width;
	}

	&.align-center {
		text-align: center;

		[class*="pure-u"] {
			float: none;
		}
	}
}

