// Component gig-details
@include component(gig-details) {
	@extend %clearfix;

	padding-bottom: 40px;

	// Part __heading
	@include part(heading) {
		color: $heading-primary-color;
		font-size: 1.4em;
		margin-bottom: 2em;
	}

	// Part __description
	@include part(description) {
		line-height: 1.5;
		margin-bottom: 2em;
	}

	// Part __attributes
	@include part(attributes) {
		line-height: 1.5;
		margin-bottom: 2em;

		// Part __name
		@include part(name) {
			clear: both;
			color: $text-secondary-color;
			font-weight: 600;
			margin-bottom: 0.75em;
		}

		// Part __value
		@include part(value) {
			margin-bottom: 0.75em;

			// Part __rewards
			@include part(rewards) {
				li {
					margin-bottom: 0.5em;
				}
			}
		}
	}
}