// Component field
@include component(field) {
	position: relative;

	// Part __autosuggest
	@include part(autosuggest) {

		// Part __results
		@include part(results) {
			background-color: #ffffff;
			display: none;
			position: absolute;
				top: 100%;
				left: 0;
				z-index: $zindex-autosuggest;
			box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.35);
			width: 100%;

			// Part __item
			@include part(item) {
				cursor: pointer;
				border-bottom: 1px solid $border-secondary-color;
				padding: 12px 15px;
				
				&:hover {
					background-color: $brand-tertiary-color-lighter;
				}

				// highlight
				strong {
					background-color: #eeff76;
					color: $text-secondary-color;
					font-weight: normal;
				}
			}

			// Part --active
			@include option(active) {
				display: block;
			}

			// Component itemlist
			@include component(itemlist) {
				margin-bottom: 0;
			}
		}
	}
}