// Component account
@include component(account) {
	float: right;
	height: $header-height;

	// Part __avatar
	@include part(avatar) {
		position: relative;
		display: block;
		width: 40px;
		height: 40px;
	}

	// Part __counter
	@include part(counter) {
		@extend %circle;

		background-color: $brand-primary-color;
		color: #ffffff;
		display: block;
		font-size: 0.7em;
		font-weight: 100;
		letter-spacing: 0;
		overflow: hidden;
		pointer-events: none;
		position: absolute;
			left: -10px;
			top: -5px;
		text-align: center;
		vertical-align: middle;
		width: 20px;
		height: 20px;

		// Part __value
		@include part(value) {
			display: block;
			line-height: 22px;
			width: 100%;
			height: 100%;
		}
	}

	// Part __summary
	@include part(summary) {
		@extend %clearfix;

		display: none;
		border-bottom: 1px solid $border-secondary-color;
		padding: 25px 0;
		
		@media all and (min-height: $screen-sm-min-height) {
			display: block;
		}

		// Part __name
		@include part(name) {
			color: $text-secondary-color;
			float: left;
			font-size: 1.5em;
			font-weight: 500;
			letter-spacing: -1px;
			padding-left: 10px;
			max-width: (200% / 3);
		}

		// Part __points
		@include part(points) {
			color: $text-tertiary-color;
			float: right;
			font-size: 0.8em;
			font-weight: 300;
			letter-spacing: -0.5px;
			margin-top: 3px;
			padding-right: 10px;
			max-width: (100% / 3);

			span {
				font-size: 1.6em;
			}
		}
	}

	// Component dropdown
	@include component(dropdown) {
		z-index: $zindex-header-dropdown;
		padding-top: 10px;

		// Part __content
		@include part(content) {
			top: $header-height;
			width: $header-dropdown-width;

			@media all and (min-width: $screen-md-min) {
				width: $header-dropdown-width-md;
			}
		}
	}

	// Component itemlist
	@include component(itemlist) {
		clear: both;
		margin-bottom: 0;

		// Part __more
		@include part(more) {
			color: $text-secondary-color;
			display: block;
			float: none;
			font-size: 0.8em;
			text-align: center;
			border-bottom: 1px solid $border-secondary-color;
			margin: 0;
			padding: 10px;

			&:hover {
				color: $text-primary-color;
			}
		}
	}

	// Component notification
	@include component(notification) {
		border-bottom: 1px solid $border-secondary-color;

		// Part __sender
		@include part(sender) {
			width: $notification-sender-size;
			height: $notification-sender-size;

			@media all and (min-height: $screen-sm-min-height) {
				width: $notification-sender-size-sm;
				height: $notification-sender-size-sm;
			}
		}

		// Part __message
		@include part(message) {
			@extend %truncate;

			margin-top: -$notification-sender-size;
			border-left: $notification-sender-size solid transparent;
			padding-top: 6px;
			min-height: $notification-sender-size;

			@media all and (min-height: $screen-sm-min-height) {
				//@extend %truncate-reset;
				white-space: normal;
				overflow: visible;

				margin-top: -$notification-sender-size-sm;
				border-left: $notification-sender-size-sm solid transparent;
				padding-top: 7px;
				min-height: $notification-sender-size-sm;
			}
		}
	}

	// Part __links
	@include part(links) {
		@extend %clearfix;

		// Part __settings
		@include part(settings) {
			color: $text-secondary-color-dark;
			float: left;
			overflow: hidden;
			width: 33%;
			height: 40px;

			&:hover {
				background-color: $brand-tertiary-color-lighter;
			}

			// icon
			.fa {
				font-size: 2em;
				text-align: center;
				padding: 7px 0;
				width: 100%;
				height: 40px;
			}
		}

		// Part __logout
		@include part(logout) {
			color: $text-tertiary-color;
			float: right;
			font-size: 0.8em;
			line-height: 20px;
			margin: 10px 0;
			padding-right: 10px;
			height: 20px;
		}
	}
}