// Component question
@include component(question) {
	@extend %clearfix;

	position: relative;
	margin-bottom: 20px;

	// Component heading-row
	@include component(heading-row) {
		margin-top: 0;
		margin-bottom: 4px;
	}

	// Part __title
	@include part(title) {
		float: left;
		font-size: 1.1em;
		line-height: 21px;
		margin: -10px;
		max-width: 80%;

		a {
			display: block;
			padding: 10px;

			&:hover {
				text-decoration: underline;
			}
		}
	}

	// Part __id
	@include part(id) {
		color: $text-tertiary-color;
		float: right;
		font-size: 1.1em;
		line-height: 21px;
	}

	// Part __answer
	@include part(answer) {

		// Component dropdown
		@include component(dropdown) {

			// Part __content
			@include part(content) {
				> * {
					padding: 10px;
				}
			}
		}
	}

	// Part __related
	@include part(related) {
		background-color: lighten($brand-secondary-color-lighter, 32%);
		color: $text-secondary-color;
		border-left: 2px solid lighten($brand-secondary-color-lighter, 5%);
		padding: 25px 15px;

		// section heading
		h4 {
			color: $text-secondary-color;
			font-size: 1.2em;
			text-transform: uppercase;
			margin-bottom: 20px;
		}

		dl {
			@extend %clearfix;
		}

		// id
		dt {
			clear: both;
			float: left;
			margin-bottom: 1em;
			padding-right: 10px;
			width: 80px;
		}

		// title
		dd {
			float: left;
			margin-bottom: 1em;

			a {
				&:hover {
					text-decoration: underline;
				}
			}
		}
	}

	// Part __search
	@include part(search) {
		float: left;
		max-width: 80%;
	}
}

// Component field
@include component(field) {

	// Part __autosuggest
	@include part(autosuggest) {

		// Part __results
		@include part(results) {

			// Component question
			@include component(question) {
				margin-bottom: 0;

				// Part __id
				@include part(id) {
					font-size: 1em;
					line-height: normal;
				}
			}
		}
	}
}