// box-sizing
html {
	box-sizing: border-box;
}
*, *:before, *:after {
	box-sizing: inherit;
}

// Make all margins and padding explicitly defined
* {
	margin: 0;
	padding: 0;
}

// Defaults
html {
	color: $text-primary-color;
	font-family: $font-primary;
	font-size: $font-size;
}

a {
	color: inherit;
	text-decoration: none;

	// Gets rid of tap active state
	-webkit-tap-highlight-color: transparent;
}

ul {
	list-style-type: none;
	padding: 0;
	li {
		list-style-type: none;
	}
}

h1, h2, h3, h4, h5, h6 {
	margin: 0;
}

img {
	max-width: 100%;
	max-height: 100%;
}

// Extendables

%clearfix {
	display: block;

	&:after {
		content: "";
		clear: both;
		display: table;
	}
}

// No Text Select
%no-select {
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

// Circle
%circle {
	border-radius: 50%;
}

// Truncate
%truncate {
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}
%truncate-reset {
	white-space: normal;
	overflow: visible;
}