$post-author-size:    40px;
$post-author-size-sm: 80px;

// Component post
@include component(post) {
	@extend %clearfix;

	position: relative;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 40px;
	max-width: 600px;

	// Part __message
	@include part(message) {
		position: relative;
		margin-bottom: 10px;
		min-height: $post-author-size;

		@media all and (min-width: $screen-sm-min) {
			min-height: $post-author-size-sm;
		}

		// Part __author
		@include part(author) {
			position: absolute;
				left: 0;
				top: 0;
				z-index: 10;
			width: $post-author-size;
			height: $post-author-size;

			@media all and (min-width: $screen-sm-min) {
				width: $post-author-size-sm;
				height: $post-author-size-sm;
			}
		}

		// Part __event, __timestamp, __content
		@include part(event, timestamp, content) {
			border: 0;
			border-left: $post-author-size solid transparent;
			padding-left: 10px;
			width: 100%;

			@media all and (min-width: $screen-sm-min) {
				border-left: $post-author-size-sm solid transparent;
			}
		}

		// Part __event
		@include part(event) {
			color: $text-secondary-color;
			font-size: 1.3em;
			line-height: 1.2;
			margin-bottom: 0.5em;

			a {
				color: $brand-primary-color;
			}
		}

		// Part __timestamp
		@include part(timestamp) {
			color: $text-tertiary-color;
			display: block;
			font-size: 0.8em;
			margin-bottom: 0.5em;
		}

		// Part __content
		@include part(content) {
			color: $text-secondary-color;
			line-height: 1.6;
			border-left: 0;
			margin-top: 0.8em;
			margin-bottom: 0.8em;
			padding-left: 0;

			@media all and (min-width: $screen-sm-min) {
				border-left: $post-author-size-sm solid transparent;
				padding-left: 10px;
			}

			a {
				color: $brand-tertiary-color;
			}
		}
	}

	// Component post-options
	@include component(post-options) {
		padding: 15px 10px;
	}
}