$entity-icon-size: 20px !default;

// Component entity
@include component(entity) {
	margin: 12px 0;

	// Part __icon
	@include part(icon) {
		display: inline-block;
		font-size: $entity-icon-size;
		line-height: $entity-icon-size;
		vertical-align: middle;
		text-align: center;
		width: $entity-icon-size;
		height: $entity-icon-size;

		// icon list
		// http://brandcolors.net/
		&.fa-facebook-square {
			color: #3b5998;
		}
		&.fa-twitter {
			color: #55acee;
		}
		&.fa-instagram {
			color: #3f729b;
		}
		&.fa-buffer {
			color: #323b43;
		}

		// adjacent label
		& + label {
			vertical-align: middle;
			margin-left: 10px;
		}

		// Option --add
		@include option(add) {
			background-color: $brand-tertiary-color-lighter;
			cursor: pointer;
			position: relative;
			border-radius: 3px;

			// plus
			&:before,
			&:after {
				content: "";
				position: absolute;
			}

			// plus vertical bar
			&:before {
				left: 50%;
				top: 5px;
				border-left: 2px solid $brand-tertiary-color;
				margin-left: -1px;
				height: 10px;
			}

			// plus horizontal bar
			&:after {
				top: 50%;
				left: 5px;
				border-top: 2px solid $brand-tertiary-color;
				margin-top: -1px;
				width: 10px;
			}

			// adjacent label
			& + label {
				cursor: pointer;
			}
		}
	}

	// Part __name
	@include part(name) {
		vertical-align: middle;
		margin-left: 10px;
	}

	// Part __info
	@include part(info) {
		display: inline-block;
		vertical-align: middle;
		margin-left: 10px;
	}

	// Component field
	@include component(field) {

		// Part __label
		@include part(label) {
			color: $text-primary-color;
		}
	}
}