// Component dashboard
@include component(dashboard) {
	text-align: center;
	box-shadow: inset 0 8px 8px -8px rgba(0, 0, 0, 0.25),
	            inset 0 -8px 8px -8px rgba(0, 0, 0, 0.25);
	padding: 1px 0 20px 0;

	// Component heading-row
	@include component(heading-row) {
		margin-bottom: 0;

		// Part __heading
		@include part(heading) {
			color: $text-secondary-color;
			font-size: 2em;
		}
	}

	// Part __metric
	@include part(metric) {
		text-align: center;
		padding: 20px;

		// Part __value
		@include part(value) {
			color: $brand-secondary-color;
			display: block;
			font-size: 3.5em;
		}

		// Part __label
		@include part(label) {
			color: $text-secondary-color;
			display: block;
			font-size: 1.1em;
		}
	}
}