// Component checklist
@include component(checklist) {
	margin-top: 0;

	// Part __item
	@include part(item) {
		margin-bottom: 1.2em;

		// Component field
		@include component(field) {

			// Part __label
			@include part(label) {
				color: $text-secondary-color;
				font-size: 1.3em;
				font-weight: 300;
				line-height: 1.6;
			}
		}
	}
}