$menu-size: 80% !default;
$menu-size-sm: 50% !default;
$menu-size-md: 35% !default;
$menu-size-lg: 25% !default;

// Component menu
@include component(menu) {
	position: fixed;
	top: 0;
	bottom: 0;
	z-index: $zindex-header-menu;
	margin-left: -$menu-size;
	width: $menu-size;

	transition: left 0.4s ease;

	@media all and (min-width: $screen-sm-min) {
		margin-left: -$menu-size-sm;
		width: $menu-size-sm;
	}
	@media all and (min-width: $screen-md-min) {
		margin-left: -$menu-size-md;
		width: $menu-size-md;
	}
	@media all and (min-width: $screen-lg-min) {
		margin-left: -$menu-size-lg;
		width: $menu-size-lg;
	}

	// Default --inactive
	left: 0;

	// Part __overlay, __underlay
	@include part(overlay, underlay) {
		opacity: 0;
		pointer-events: none;
	}

	// Option --active
	@include option(active) {
		left: $menu-size;

		@media all and (min-width: $screen-sm-min) {
			left: $menu-size-sm;
		}
		@media all and (min-width: $screen-md-min) {
			left: $menu-size-md;
		}
		@media all and (min-width: $screen-lg-min) {
			left: $menu-size-lg;
		}

		// Part __overlay, __underlay
		@include part(overlay, underlay) {
			opacity: 1;
			pointer-events: auto;
		}
	}

	// Part __overlay
	@include part(overlay) {
		background: #ffffff;
		position: relative;
			z-index: $zindex-header-menu;
		box-shadow: 2px 0 2px 0 rgba(0, 0, 0, 0.3);
		height: 100%;
		width: 100%;

		transition: opacity 0.4s;

		// Component logo
		@include component(logo) {
			position: relative;
				left: 10px;
		}
	}

	// Part __underlay
	@include part(underlay) {
		background-color: rgba(0, 0, 0, 0.3);
		position: fixed;
			left: 0;
			top: 0;
			z-index: $zindex-header-menu-underlay;
		height: 100%;
		width: 100%;

		transition: opacity 0.3s;
	}

	// Component logo
	@include component(logo) {
		margin: 14px 0;
	}

	// Part __profile
	@include part(profile) {
		padding: 0.6em 20px;

		// Part __avatar
		@include part(avatar) {
			@extend %circle;
			vertical-align: middle;
		}

		// Part __name, __credits
		@include part(name, credits) {
			vertical-align: top;
			margin-top: 12px;
		}

		// Part __name
		@include part(name) {
			color: $text-primary-color;
			display: inline-block;
			padding-left: 0.6em;
		}

		// Part __credits
		@include part(credits) {
			color: $text-secondary-color;
			float: right;
		}
	}

	// Part __nav
	@include part(nav) {
		clear: both;

		// Part __item
		@include part(item) {
			color: $text-primary-color;
			display: block;
			position: relative;
			border: none;
			padding: 0.9em 10px;

			// Part __icon
			@include part(icon) {
				color: $menu-icon-color;
				display: inline-block;
				text-align: center;
				min-width: 40px;

				// hover state on parent
				:hover > & {
					//color: lighten($menu-icon-color, 30%);
					opacity: 0.7;
				}
			}

			// Part __label
			@include part(label) {
				padding-left: 0.6em;
			}
		}
	}
}