$user-cover-height: 120px !default;
$user-avatar-size:  120px !default;
$user-avatar-gap:   35px !default;

$user-cover-height-lg: 250px !default;
$user-avatar-size-lg:  200px !default;
$user-avatar-gap-lg:   100px !default;

$user-rank-gold-width: 36% !default;
$user-rank-silver-width: 32% !default;
$user-rank-bronze-width: 32% !default;
$user-rank-ribbon-width: 54% !default;
$user-rank-margin-increment: 10% !default;

// Component user
@include component(user) {
	@extend %clearfix;

	position: relative;
	text-align: center;
	margin-bottom: 10px;

	// Part __bg, __cover
	@include part(bg, cover) {
		position: relative;
	}

	// Part __bg
	@include part(bg) {
		background-color: #ffffff;
		padding-bottom: 20px;
		margin-bottom: 20px;
	}

	// Part __cover
	@include part(cover) {
		background-color: #efefef;
		background-position: center top;
		background-repeat: no-repeat;
		background-size: cover;
		overflow: hidden;
		height: $user-cover-height;
	}

	// Part __avatar
	@include part(avatar) {
		display: inline-block;
		position: relative;
			z-index: 10;
		box-shadow: 0 1px 8px 0 rgba(0, 0, 0, 0.75);
		margin-top: -($user-cover-height - $user-avatar-gap);
		margin-bottom: 15px;
		width: $user-avatar-size;
		height: $user-avatar-size;
		
		img {
			display: block;
			width: 100%;
		}
	}

	// Part __name
	@include part(name) {
		color: $heading-primary-color;
		font-size: 1.5em;
		margin-bottom: 12px;
	}

	// Part __summary
	@include part(summary) {
		line-height: 1.5;
		margin-bottom: 20px;
		padding: 0 10%;

		@media all and (min-width: $screen-sm-min) {
			padding: 0 20%;
		}

		@media all and (min-width: $screen-lg-min) {
			padding: 0 10%;
		}
	}

	// Part __entities
	@include part(entities) {
		text-align: left;
		margin-bottom: 2em;

		// Component entity
		@include component(entity) {
			color: $text-tertiary-color;
			font-size: 1.1em;
		}
	}

	// Part __rank
	@include part(rank) {
		display: block;
		font-size: 1em;
		margin-bottom: 0.6em;
	}

	// Part __points
	@include part(points) {
		color: $text-secondary-color;
		display: block;
		font-size: 0.8em;
		margin-bottom: 0.6em;
		
		strong {
			font-size: 1.2em;
			font-weight: normal;
		}
	}

	// Part --large
	@include option(large) {
		margin-bottom: 60px;

		// Part __cover
		@include part(cover) {
			height: $user-cover-height-lg;
		}

		// Part __avatar
		@include part(avatar) {
			margin-top: -($user-cover-height-lg - $user-avatar-gap-lg);
			margin-bottom: 25px;
			width: $user-avatar-size-lg;
			height: $user-avatar-size-lg;
		}

		// Part __name
		@include part(name) {
			font-size: 2.2em;
			margin-bottom: 20px;
		}

		// Part __summary
		@include part(summary) {
			margin-bottom: 20px;
		}
	}

	// Part --dashboard
	@include option(dashboard) {
		text-align: left;
		margin: 20px 0;

		// Part __avatar
		@include part(avatar) {
			float: left;
			box-shadow: none;
			border: 1px solid $border-secondary-color;
			margin-top: 0;
			margin-right: 20px;
			width: $user-avatar-size;
			height: $user-avatar-size;
		}

		// Part __name
		@include part(name) {
			font-size: 2.2em;
			margin-bottom: 20px;
			padding-top: 10px;
		}

		// Part __actions
		@include part(actions) {
			@extend %clearfix;

			li {
				display: inline-block;
				font-size: 1.6em;
				margin-right: 0.5em;
				margin-bottom: 0.5em;
			}

			a {
				color: $text-primary-color-lighter;
				text-decoration: none;
			}

			a:hover {
				color: $text-primary-color;
				text-decoration: underline;
			}
		}
	}

	// Part --gold, --silver, --bronze
	@include option(gold, silver, bronze) {
		margin-bottom: 12%;

		// ribbon
		&:before {
			content: "";
			background-image: url(../images/cut.png);
			background-position: center bottom;
			background-repeat: no-repeat;
			position: absolute;
				left: ((100 - $user-rank-ribbon-width) / 2);
			padding-top: 1px;
			width: $user-rank-ribbon-width;
			height: 100%;
		}

		// Part __bg
		@include part(bg) {
			border-left: 1px solid #cccccc;
			border-right: 1px solid #cccccc;
			border-bottom: 1px solid #cccccc;
			box-shadow: 0 6px 6px 0 rgba(0, 0, 0, 0.2);
		}
	}

	// Part --gold
	@include option(gold) {
		margin-top: ($user-rank-margin-increment * 1);
		margin-left: $user-rank-silver-width; // position gold at second column
		width: $user-rank-gold-width;

		// ribbon
		&:before {
			background-color: #d3af37;
			top: 150px;
		}

		// Part __rank
		@include part(rank) {
			font-size: 1.6em;
		}
	}

	// Part --silver
	@include option(silver) {
		margin-top: ($user-rank-margin-increment * 2);
		margin-left: -(100 - $user-rank-silver-width); // position silver at first column
		width: $user-rank-silver-width;

		// ribbon
		&:before {
			background-color: #d1d4db;
			top: 130px;
		}

		// Part __rank
		@include part(rank) {
			font-size: 1.3em;
		}
	}

	// Part --bronze
	@include option(bronze) {
		margin-top: ($user-rank-margin-increment * 3);
		width: $user-rank-bronze-width;

		// ribbon
		&:before {
			background-color: #d48538;
			top: 110px;
		}

		// Part __rank
		@include part(rank) {
			font-size: 1.2em;
		}
	}
}

// Component main
@include component(main) {

	// Part __aside
	@include part(aside) {

		// Component user
		@include component(user) {

			// Part --large
			@include option(large) {

				// Part __avatar
				//@include part(avatar) {
				.user__avatar {

					// reduce size of avatar in sidebar only
					@media all and (min-width: $screen-lg-min) {
						margin-top: -($user-cover-height - $user-avatar-gap);
						width: $user-avatar-size;
						height: $user-avatar-size;
					}
				}
			}
		}
	}
}